.NavbarItems{
    background: #ffffff;
    height: 50px;
    display: flex;
    font-size: 1rem;
    box-shadow: rgba(50, 50, 93, 0.14902) 0px 1px 0px,
    rgba(0, 0, 0, 0.0196078) 0px 1px 0px;
  border: 0;
  outline: 0;
  position: sticky; top: 0;
  z-index: 1;
}

.logo_m{
    display: none; 
}
.logo{

}
.logo_font{
  font-family: 'Anton', sans-serif;
  color: #3f6061;
  text-align: center;
  font-size: 2.5rem;
}
.nav-menu{
    display: grid;
    grid-template-columns: repeat(6, auto);
    grid-gap: 40px;
    list-style: none;
    text-align: center;
    width: 100vw;
    justify-content: center;
    margin-right: 1rem;
}

  ul {
    list-style: none;
    margin: 0;
    padding: 0;
  }
  
  a {
    
    text-decoration: none;
    color: black;
  }
  .menu-item {
    height: 50px;
    display: flex;
    align-items: center;
    border-radius: var(--border-radius);
    transition: background var(--speed);
    padding: 0.5rem;
  }
  .menu_m{
    display: none;
  }
  .topbar{
    width: 70vw
  }
  
  @media screen and (max-width: 960px) {
   
      .nav-menu{
        display: flex;
        flex-direction: column;
        width: 100vw;
        height: 400px;
        position: absolute;
        top: 80%;
        left: -100%;
        opacity: 1%;
        transition: all 0.5s ease-out;
    }
    .logo_font{
      font-family: 'Anton', sans-serif;
      color: #3f6061;
      text-align: center;
      font-size: 1rem;
    }
    .topbar{
      width: 60vw
    }
    .nav-menu.active{
        background-color: #ffffff;
        left: 0;
        opacity: 1;
        transition: all 0.5s ease-out;
        z-index: 1;
        
    }
      .menu{
    display: none;
      }
      .menu_m{
        display: inline;
        float: right;
       width: 80vw;
      }
      .navbar{
        display: none;
      }
      
  }